.modal {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
}

.modal-content {
  width: 70%;
  position: relative;
  top: 100px;
  left: 50%;
  transform: translate(-50%);
  padding: 20px;
  border-radius: 14px;
  background: #a1cdcc;
}

.modal-open {
  display: flex;
}

@media all and (max-width: 645px) {
  .modal-content {
    width: 90%;
  }
}